<template>
  <v-card
    class="site-schedule-list-item elevation-1 d-flex flex-column"
    :class="{ actived: schedule.actived, finished: false }"
    dark
    @click="choosed"
  >
    <div class="site-schedule-list-item-date">
      {{ formatDate(schedule.start_date) }}
    </div>
    <div class="site-schedule-list-item-barbershop">
      {{ schedule.barbershop.name}}
    </div>
    <div>
      <small
      :class="'site-schedule-list-item-status-' + schedule.status"
      class="site-schedule-list-item-status d-inline-block"
    >
      {{ getStatusText(schedule.status ) }}
    </small>
    </div>
  </v-card>
</template>

<script>
import DateMixin from '@/mixins/DateMixin'
import ScheduleMixin from '@/mixins/ScheduleMixin'

export default {
  name: 'ScheduleItem',
  mixins: [ DateMixin, ScheduleMixin ],
  props: {
    schedule: { type: Object, required: true }
  },
  methods: {
    choosed () {
      this.$emit('scheduleChoosed', this.schedule)
    },
    formatDate (startDate) {
      if (!startDate) return ''
      let date = new Date(startDate)
      return this.formatDateTimeToStringPtBr(date)
    }
  }
}
</script>

